<template>
  <div class="home">
    <div class="enter">
	  <h2 class="weclome">WORK HARD IN SILENCE. LET SUCCESS BE YOUR NOISE.</h2>
		  <router-link to="/photo" class="btn btn1">摄影</router-link>
		  <router-link to="/page" class="btn btn2">文章</router-link>
    </div>
    <canvas id="myCanvas" ref="canv"></canvas>
  </div>
</template>

<style lang="less">
	.home{
	
      	position: relative;
		canvas {
			/*background-color: #000;*/
			background-image: url(../../public/sf4.jpg);
			background-position: center center;
			background-size: cover;
    	}
        .enter{
          position:absolute;
          top:45%;
          left:50%;
          transform: translate(-50%, -50%);
          width: 300px;
          height: 400px;
          background-color: rgba(255,255,255,0);
		}
		.weclome{
			color:#fff;
			font-size:30px;
			font-weight: 500;
			width: 190px;
		}
		.btn{
			display: block;
			width: 80px;
			height: 36px;
			line-height: 36px;
			background-color:rgba(92,92,92,.5);
			text-align: center;
			font-size:18px;
			color:#fff;
			border-radius: 5px;
			border:0.5px solid #fff;
		
		}
			.btn:hover{
				background-color:#FF4E50;
			}
		.btn1{
			position:absolute;
			bottom:90px;
			left:0px;
		}
		.btn2{
			position:absolute;
			bottom:90px;
			left:100px;
		}
		
	}
</style>
<script>
  export default{
	  mounted(){
		  console.log(canvas);
		  let canvas = this.$refs.canv;
		  // 获取当前视口的宽
			var width = document.documentElement.clientWidth;
			// 获取当前时候的高
			var height = document.documentElement.clientHeight;

			// 赋值给canvas
			canvas.width = width;
			canvas.height = height;

			// 获取画笔
			var ctx = canvas.getContext("2d");

			// 改变填充色
			ctx.fillStyle = "white";

			// 改变描边色
			ctx.strokeStyle = "rgba(255, 255, 255, .5)";

			// 改变线宽
			ctx.lineWidth = .2;


			// 定义星星类
			function Star(ctx, x, y, r) {
				this.ctx = ctx;
				this.x = x;
				this.y = y;
				this.r = r;
				this.x_speed = (parseInt(Math.random() * 3) + 1) * Math.pow(-1, parseInt(Math.random() * 10) + 1) / 5;
				this.y_speed = (parseInt(Math.random() * 3) + 1) * Math.pow(-1, parseInt(Math.random() * 10) + 1) / 5;
			}

			// 方法要写在原型中
			// 渲染
			Star.prototype.render = function() {
				// 开启路径
				this.ctx.beginPath();
				// 绘制弧
				this.ctx.arc(this.x, this.y, this.r, 0, Math.PI * 2);
				// 闭合路径
				this.ctx.closePath();
				// 填充
				this.ctx.fill();
			}

			// 清屏
			Star.prototype.clear = function() {
				this.ctx.clearRect(0, 0, canvas.width, canvas.height);
			}

			// 移动
			Star.prototype.move = function() {
				this.x -= this.x_speed;
				this.y -= this.y_speed;
			}

			// 改变x方向
			Star.prototype.changeX = function() {
				this.x_speed = - this.x_speed;
			}

			// 改变y方向
			Star.prototype.changeY = function() {
				this.y_speed = - this.y_speed;
			}

			// 初始化星星对象
			var star = new Star(ctx, Math.random() * width, Math.random() * height, 1);
			// star.render();


			// 创建星星对象
			var mouse_star = new Star(ctx, 0, 0, 2);
			// 给document条件mousemove事件
			document.onmousemove = function(e) {
				// 获取鼠标位于视口中的位置
				var x = e.clientX;
				var y = e.clientY;
				// 赋值鼠标星星对象的x和y
				mouse_star.x = x;
				mouse_star.y = y;
			}



			// 定义数组， 用于存储所有的星星对象
			var arr = [];

			for (var i = 0; i < 100; i++) {
				arr.push(new Star(ctx, Math.random() * width, Math.random() * height, 1));
			}


			// 开启定时器
			var timer = setInterval(function() {
				star.clear();
				// 渲染鼠标星星对象
				mouse_star.render();

				// 清屏
				arr.forEach(function(value, index) {
					// 移动
					value.move();
					// 判断
					if (value.x < 0 || value.x > width) {
						// 执行changeX
						value.changeX();
					} 

					if (value.y < 0 || value.y > height) {
						// 执行changeY
						value.changeY();
					}
					// 渲染
					value.render();
					})


				// 循环判断
				arr.forEach(function(value, index) {
					// value表示每一个星星对象， 我们应该拿着这个value与其它的所有星星对象进行比较
					for (var i = index + 1; i < arr.length; i++) {
						if (Math.abs(value.x - arr[i].x) < 50 && Math.abs(value.y - arr[i].y) < 50) {
							// 当满足条件的时候要连线
							line(value.x, value.y, arr[i].x, arr[i].y);
						}
					}

					// 判断鼠标星星对象与其它所有星星对象之间的关系
					if (Math.abs(value.x - mouse_star.x) < 150 && Math.abs(value.y - mouse_star.y) < 150) {
						// 连线
						line(value.x, value.y, mouse_star.x, mouse_star.y);
					}
				})
			}, 20)


			// 封装一个函数传递两个点， 绘制两点之间的线段
			function line(x1, y1, x2, y2) {
				// 开启路径
				ctx.beginPath();
				// 移动画笔到某个位置
				ctx.moveTo(x1, y1);
				ctx.lineTo(x2, y2);
				// 闭合路径
				ctx.closePath();
				// 描边
				ctx.stroke();
			}


			// 给document添加onclick事件
			document.onclick = function(e) {
				for (var i = 0; i < 5; i++) {
					arr.push(new Star(ctx, e.clientX, e.clientY, 1))
					// 删除数组的最后一项
					arr.shift();
				}	
			}
			}
  }
</script>  








