import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Page from './views/Page.vue'
import Photo from './views/Photo.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/page',
      name: 'page',
      component: Page
    },
    {
      path: '/photo',
      name: 'photo',
      component:Photo
    }
  ]
})
